<template>
    <div id="warningPage">
        <div class="title">告警记录</div>
        <div class="warningPage-main">
            <div class="opreationbox">
                <div class="searchbox">
                    <el-form ref="searchform" :model="searchform" class="searchform" :inline="true">
                        <el-form-item label="事件：" label-width="54px">
                            <el-select v-model="searchform.eventId"
                                    size="small"
                                    clearable
                                    placeholder="全部"
                                    style="width:180px;margin-right:15px;">
                                <el-option
                                    v-for="(item,index) in allEventTypeArr"
                                    :key="index"
                                    :label="item.eventName"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="日期：" label-width="54px">
                            <el-date-picker
                                size="small"
                                v-model="searchform.dateTime"
                                type="daterange"
                                align="right"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期"
                                @change="consolechange"
                                value-format='yyyy-MM-dd'
                                style="width:240px"
                                >
                                >
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="位置：" label-width="54px">
                            <el-input placeholder="全部"
                                      size="small"
                                      v-model="searchform.deviceName" 
                                      clearable 
                                      style="width:150px"/>
                        </el-form-item>
                        <el-form-item class="search-button">
                            <el-button type="primary" 
                                       class="btn"
                                       size="small"
                                       @click="searchtabledata('search')"
                                       style="boder-raduis:0;">查询</el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="buttonbox">
                    <!-- <el-button class="btn" @click="openSetting">设置</el-button> -->
                    <div @click="deleteRow(multipleSelection)" title="批量删除"></div>
                </div>
            </div>
            <!-- 表格 -->
            <div class="table-box" ref="tableBox">
                <el-table
                    ref="warningTable"
                    :data="tableData"
                    v-loading="tableBoxLoading"
                    @selection-change="handleSelectionChange"
                    :header-cell-style="{background:'rgba(245,248,252,1)'}"
                    style="width: 100%"
                    :row-key="getRowKeys"
                    >
                    <el-table-column
                        fixed
                        type="selection"
                        width="55"
                        reserve-selection
                    ></el-table-column>
                    <el-table-column
                        type="index"
                        width="120"
                        label="序号"
                        align="center">
                    </el-table-column>
                    <el-table-column
                        v-for="(item,index) in tableDataLabel"
                        :key="index"
                        :label="item.name"
                        :prop="item.property"
                        align="center"
                    >
                    <template slot-scope="scope" >
                        <span v-if="index == 0">{{scope.row.eventName}}</span>
                        <span v-if="index == 1">{{scope.row.time}}</span>
                        <span v-if="index == 2">{{scope.row.deviceName}}</span>
                        <div v-if="index == 3">
                            <img :src="scope.row.handle == 1 ? handleImgUrl.processed : handleImgUrl.unprocessed" alt="">
                        </div>
                        <div v-if="index == 4">
                            <span v-if="scope.row.handleDetail">{{scope.row.handleDetail}}</span>
                        </div>
                    </template>
                    </el-table-column>
                    <el-table-column
                        label="操作"
                        align="center"
                        width="140"
                    >
                        <template slot-scope="scope">
                            <div style="display:flex;justify-content: space-between;">
                                <div style="cursor:pointer;"
                                    title="详情"
                                    class="row-detail"
                                    @click="openDetail(scope.row)">
                                </div>
                                <div style="cursor:pointer;"
                                    title="删除"
                                    class="delete-row"
                                    @click="deleteRow(scope.row)">
                                </div>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination-box">
                    <el-pagination
                        ref="pagination"
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[5,10,20]"
                        :page-size.sync="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
        <el-dialog
            title="告警详情"
            :visible.sync="detailDialogVisible"
            width="650px"
            :before-close="handleDetailClose"
            class="detaiDialog-box">
            <div class="detailDialogContent">
                <div class="detailImg-box">
                    <div v-if="this.detailType == 1"></div>
                    <div>
                      <img :src="handleImgUrl.processed" alt="">
                    </div>
                </div>
                <div class="detailTxt-box">
                    <div class="detailTxt-compare" 
                         v-if="this.detailType == 1">相似度：98%</div>
                    <div class="detailTxt-compare" v-else></div>
                    <div class="detailTxt-main"
                         v-loading="detailLoading">
                      <!-- <div v-for="(item,index) in detailKey"
                           :key="index"
                           class="detailTxt-main-per">
                        <span class="detailTxt-key">{{item.name}}</span>
                        <span class="detailTxt-value">
                          <span v-if="rowDetail[item.property]">{{rowDetail[item.property]}}</span>
                        </span>
                      </div> -->
                      <div v-for="(item,key,index) in rowDetail"
                           :key="index"
                           class="detailTxt-main-per">
                           <!-- <template slot-scope="scope">
                            <span class="detailTxt-key">{{filterDetailKey(scope)}}</span>
                           </template> -->
                          <span class="detailTxt-key">{{filterDetailKey(key)}}</span>
                        <span class="detailTxt-value">
                          <!-- <span>{{item}}</span> -->
                          <span>{{filterDetailValue(key,item)}}</span>
                        </span>
                      </div>
                    </div>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false"
                           style="padding: 10px 24px;">确 定</el-button>
            </span>
        </el-dialog>
        </div>
</template>

<script>
import APICONFIG from '../../../../../../config/api.js';
// import moment from 'moment'

export default{
  data() {
    return{
      netAPI: null,
      eventAPI: null,
      batchdisabled: true,
      tableBoxLoading: false,
      detailDialogVisible: false,
      detailLoading: true,
      detailType: 1,
      allEventTypeArr: [],
      multipleSelection: [],
      rowDetail: '',  // 行详情
      tableData: [],
      tableDataLabel: [
        { name: "告警事件", property: "eventName" },
        { name: "告警时间", property: "time" },
        { name: "告警设备", property: "deviceName" },
        { name: "处理结果", property: "handle" },
        { name: "处理详情", property: "handleDetail" },
      ],
      // detailKey: ['事件', '时间', '姓名', '地点', '告警处理', '处理详情'],
      detailKey: [
        {
          name: '事件',
          property: 'eventName'
        },
        {
          name: '时间',
          property: 'time'
        },
        {
          name: '姓名',
          property: 'personId'
        },
        {
          name: '地点',
          property: 'deviceName'
        },
        {
          name: '人脸识别结果',
          property: 'recognitionResult'
        },
        {
          name: '告警处理',
          property: 'handle'
        },
        {
          name: '处理详情',
          property: 'handleDetail'
        },
      ],
    //   操作表格行图片路径
      handleImgUrl: {
        processed: require('./img/warning/processed.png'),
        unprocessed: require('./img/warning/unprocessed.png')
      },
      currentPage: 1,
      pageSize: 10,
      total: 0,
      searchform: {
        eventId: '',
        dateTime: '',
        deviceName: '',
      },
      getRowKeys(row) {
        return row.id
      },
    }
  },
  created() {
    this.netAPI = APICONFIG().get("monitorSystem").warning;
    this.eventAPI = APICONFIG().get("event_manage");
    this.searchEvent();
    this.searchtabledata();
  },
  mounted() {
  },
  methods: {
    filterDetailKey(key) {
      // console.log(key)
      switch(key) {
        case 'eventName':
          return '事件';
        case 'time':
          return '时间';
        case 'deviceName':
          return '设备';
        case 'handle':
          return '告警处理';
        case 'handleDetail':
          return '处理详情';
        default:
          return null;
      }
    },
    filterDetailValue(key, value) {
      switch(key) {
        case 'eventName':
          return value;
        case 'time':
          return value;
        case 'deviceName':
          return value;
        case 'handle':
          return value == '1' ? '已处理' : '未处理';
        case 'handleDetail':
          return value;
        default:
          return null;
      }
    },
    filterTbale(row) {
      switch(row.handle) {
        case 1:
          return '已处理';
        case 0:
          return '未处理';
      }
    },
    // 查询所有事件
    searchEvent() {
      this.$http.get(this.eventAPI.find, {
        params: {
          pageNo: 1,
          pageSize: 100000,
          alarmEvent: 1
        }
      }).then(
        res => {
          if(res.body.status == 200) {
            // console.log(JSON.stringify(res.body.data))
            this.allEventTypeArr = res.body.data
          }
        }
      )
    },
    // 查询告警列表
    searchtabledata(v) {
      this.tableBoxLoading = true;
      if(v == 'search') {
        this.currentPage = 1;
      }
      let newSearch = {};
      // 时间
      if(this.searchform.dateTime) {
        newSearch.startTime = this.searchform.dateTime[0];
        newSearch.finishTime = this.searchform.dateTime[1];
      }else{
        // 如果没选择日期的话默认查当天
        // let now = moment().format('YYYY-MM-DD')
        // newSearch.startTime = now
        // newSearch.finishTime = now

        // 调试用，后期需删除
        newSearch.startTime = '2000-01-01'
        newSearch.finishTime = '2050-01-01'
      }
      // 事件
      if(this.searchform.eventId != '') {
        newSearch.eventId = this.searchform.eventId;
      }else{
        delete newSearch.eventId
      }
      // 位置
      if(this.searchform.deviceName != '') {
        newSearch.deviceName = this.searchform.deviceName;
      }else{
        delete newSearch.deviceName
      }
      newSearch.pageSize = this.pageSize;
      newSearch.pageNo = this.currentPage;
      // console.log(JSON.stringify(newSearch))
      this.$http.get(this.netAPI.find, {params: newSearch}).then(
          res => {
            if (res.body.status == 200) {
              // console.log(JSON.stringify(res.body.data))
              this.tableData = res.body.data
              this.total = res.body.total
              this.tableBoxLoading = false;
            } else {
              this.$message.warning(res.body.message);
            }
          },
            err => {
              this.$message({
                message: "服务错误" + err,
                type: "warning"
              });
            }
      )
    },
    consolechange() {

    },
    // 批量删除
    batchremove() {

    },
    // 打开详情
    openDetail(row) {
      // console.log(JSON.stringify(row))
      this.detailLoading = true
      this.$http.get(this.netAPI.detail, {params: {id: row.id}}).then(
          res => {
            if(res.body.status == 200) {
              console.log(JSON.stringify(res.body.data))
              this.rowDetail = res.body.data;
              delete this.rowDetail.createTime
              delete this.rowDetail.updateTime
              delete this.rowDetail.id
              delete this.rowDetail.deviceId
              delete this.rowDetail.personId
              delete this.rowDetail.eventId
              console.log(JSON.stringify(this.rowDetail))
              this.detailLoading = false
            }
          }
      )
      this.detailDialogVisible = true;
    },
    // 关闭详情
    handleDetailClose(done) {
      done();
    },
        // 删除行
    deleteRow(row) {
    //   console.log(JSON.stringify(row))
      let ids = [];
      if (!row) {
        this.$message({
          message: "请选中要删除设备",
          type: "warning"
        });
        return;
      }
      //判断类型
      let rowType = row instanceof Array
      if (rowType == true) {
        for (let index in row) {
          ids.push({ id: row[index].id });
        }
      } else {
        ids.push({ id: row.id });
      }
      console.log(JSON.stringify(ids))
      if(ids.length > 0) {
        this.$confirm("确定要删除选中的告警记录？", "删除提示", {
          distinguishCancelAndClose: true,
          type: 'warning',
          confirmButtonText: "删除",
          cancelButtonText: "取消"
        }).then(() => {
          this.$http.post(this.netAPI.delete, ids).then(
          res => {
            if (res.data.status == 200) {
              console.log(JSON.stringify(res.body))
              if (ids.length == this.tableData.length) {
                this.currentPage = this.currentPage - 1 > 0 ? this.currentPage - 1 : 1
                this.searchtabledata();
              } else {
                this.searchtabledata();
              }
              this.$message.success("删除成功!");
              this.$refs.warningTable.clearSelection();
            } else {
              this.$message.warning(
                  "删除失败" + res.body.message
                );
            }
          },
          err => {
            this.$message({
              message: "服务错误" + err,
              type: "warning"
            });
          }
        );
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }else{
        this.$message.warning('请选择要删除的设备!')
      }
    },
    handleSelectionChange(val) {
      if(val.length > 0) {
        this.batchdisabled = false
      }else{
        this.batchdisabled = true
      }
      this.multipleSelection = val;
    },
    handleSizeChange (pageSize) {
      this.pageSize = pageSize;
      this.searchtabledata();
    },
    handleCurrentChange (Current) {
      this.currentPage = Current;
      this.searchtabledata();
    },
  }
}
</script>

<style lang="scss" scoped>
#warningPage{
    position: absolute;
    top: 0;
    left: 30px;
    right: 30px;
    bottom: 30px;
    background: #fff;
    min-height: 361px;
    .title{
        background: #F5F8FC;
        height: 80px;
        line-height: 80px;
        font-size: 20px;
        box-sizing: border-box;
        font-family: Microsoft YaHei;
        display: flex;
        align-items: center;
    }
    .warningPage-main{
        // width: 100%;
        // height: 800px;
        position: absolute;
        top: 80px;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 30px;
        padding-bottom: 0; 
        border: 1px solid #dedede;
        box-sizing: border-box;
        background: #fff;
        box-shadow: #E9E9E9 0 2px 4px 0;

        .opreationbox{
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            .searchbox{
                .searchform{
                    display: flex;
                    height: 1.5rem;
                    align-items: center;
                }
            }
            .buttonbox{
              >div{
                margin-top: 15px; 
                cursor: pointer;
                width: 24px;
                height: 24px;
                background: url('./img/warning/batchDeletion_.png') no-repeat;
                &:hover {
                  background: url('./img/deviceManage/batchDeletion.png')  no-repeat !important;
                }
              }
            }
        }
        .table-box{
            .row-detail{
              background: url('./img/warning/details.png') no-repeat;
              height: 30px;
              width: 54px;
              &:hover {
                background: url('./img/warning/details_selected.png')  no-repeat !important;
              }
            }
            .delete-row{
              background: url('./img/warning/delete.png') no-repeat;
              height: 30px;
              width: 54px;
              &:hover {
                background: url('./img/warning/delete_selected.png')  no-repeat !important;
              }
            }
            .pagination-box {
              height: 64px;
              display: flex;
              justify-content: center;
              align-items: center;
            }
        }
    }
}
</style>
<style lang="scss">
#warningPage{
    .searchbox{
        .el-form-item__label {
            padding: 0 !important;
        }
        .search-button{
            .el-button{
                padding-bottom: 8px;
            }
        }
    }
    .el-table th, .el-table tr {
      color:rgba(37,44,53,1);
    }
    .el-dialog__header{
    font-family: Microsoft YaHei;
    }
    .detaiDialog-box{
        .el-dialog__header{
            border-bottom: 1px solid #E9EAEB;
        }
        .el-dialog__body{
          padding-bottom: 0;
          padding-top: 0;
        }
        .detailDialogContent{
            display: flex;
            height: 300px;
            box-sizing: border-box;
            // padding: 20px;
            // padding-bottom: 0;
            // padding-right: 0;
            padding-top: 20px;
            padding-left: 20px;
            .detailImg-box{
                flex: 0.9;
                height: 92%;
                box-sizing: border-box;
                padding-top: 15px;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                >div{
                  height: 50%;
                  background: #dedede;
                  &:nth-child(1){
                    margin-bottom: 5px;
                  }
                  &:nth-child(2){
                    margin-top: 5px;
                  }
                  >img{
                    height: 100%;
                    width: 100%;
                  }
                }
            }
            .detailTxt-box{
                flex: 2;
                .detailTxt-compare{
                  height: 25px;
                  font-size: 16px;
                  font-weight: 600;
                  box-sizing: border-box;
                  padding-left: 60px;
                  color: #000;
                  font-family: Microsoft YaHei;
                  margin-bottom: 10px;
                }
                .detailTxt-main{
                  height: 240px;
                  box-sizing: border-box;
                  padding-left: 60px;
                  font-size: 14px;
                  display: flex;
                  flex-direction: column;
                  .detailTxt-main-per{
                    flex: 1;
                    display: flex;
                    align-items: center;
                    >span{
                      display: inline-block;
                    }
                    .detailTxt-key{
                      width: 130px;
                      color: #92959A;
                    }
                    .detailTxt-value{
                      color: #000;
                      font-family: Microsoft YaHei;
                    }
                  }
                }
            }
        }
    }
}
</style>



